<template>
  <div style="margin-top: 10px" v-loading="fullloading" id="userManage">

    <div style="margin-top: 20px">
      <el-table :data="sysUserList" size="mini" border stripe @selection-change="handleSelectionChange" v-loading="tableLoading" style="width: 100%">
        <el-table-column type="selection" align="left">
        </el-table-column>
        <el-table-column prop="name" align="center" label="姓名">
        </el-table-column>
        <el-table-column prop="username" align="center" label="用户名">
        </el-table-column>
        <el-table-column prop="phone" align="center" label="手机号码">
        </el-table-column>
        <el-table-column prop="telephone" align="center" label="住宅电话">
        </el-table-column>
        <el-table-column prop="address" align="center" label="联系地址">
        </el-table-column>
        <el-table-column prop="remark" align="center" label="备注">
        </el-table-column>
        <el-table-column align="center" label="用户角色">
          <template slot-scope="scope">
            <el-tag v-for="role in scope.row.roles" :key="role.id" type="success"
              size="mini" style="margin-right: 5px" :disable-transitions="false">
              {{role.nameZh}}
            </el-tag>
            <el-popover
              placement="bottom"
              title="角色列表"
              width="200"
              trigger="click"
              @hide="updateRole(scope.row.id)">
              <el-select v-model="setRoleList" multiple placeholder="请选择角色">
                <el-option
                  v-for="item in roleList"
                  :key="item.id"
                  :label="item.nameZh"
                  :value="item.id">
                </el-option>
              </el-select>
              <el-button type="text" icon="el-icon-more" style="color: #09c0f6;padding-top: 0px"
                         @click="loadSetRoles(scope.row.roles)" slot="reference"></el-button>
            </el-popover>
          </template>
        </el-table-column>

        <el-table-column  v-if="$hasButton('sysUserEdit') || $hasButton('sysUserDel')" label="操作" align="center" width="300"  fixed="right">
          <template slot-scope="scope">
            <el-button v-if="$hasButton('sysUserEdit')" @click="update(scope.row,{title:'修改'})" type="primary" style="padding: 3px 4px 3px 4px;margin: 2px"
                       size="mini">修改
            </el-button>
            <el-button v-if="$hasButton('sysUserDel')" @click="deleteMsg(scope.row.id)" type="danger" style="padding: 3px 4px 3px 4px;margin: 2px"
                       size="mini">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="clearfix">
      <div class="fl" style="margin-top: 20px">

        <el-button v-if="$hasButton('sysUserAdd')" size="mini"  @click="add" type="success" icon="el-icon-search">新增</el-button>
        <el-button v-if="$hasButton('sysUserDel')" size="mini" @click="deleteAll()" type="danger" icon="el-icon-delete">删除</el-button>
      </div>
    </div>

    <!--新增或修改弹框-->
    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left" id="Bdialog">
        <el-dialog
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="50%">
          <div class="dialog_B Detail">
            <div class="" style="width: 700px;margin: auto">
              <h1 class="H1">{{dialogTitle}}</h1>
              <el-form style="margin: 0px;padding: 0px;">
                <el-form-item label="姓名：" prop="name" :label-width="labelWidth">
                  <el-input placeholder="请输入姓名" prefix-icon="el-icon-edit" v-model="sysUser.name"></el-input>
                </el-form-item>
                <el-form-item label="用户名：" prop="username" :label-width="labelWidth">
                  <el-input placeholder="请输入用户名" prefix-icon="el-icon-edit" v-model="sysUser.username"></el-input>
                </el-form-item>
                <el-form-item label="密码：" prop="" :label-width="labelWidth">
                  <el-input type = "password" placeholder="请输入密码" prefix-icon="el-icon-edit" v-model="sysUser.password"></el-input>
                </el-form-item>
                <el-form-item label="手机号码：" prop="phone" :label-width="labelWidth">
                  <el-input placeholder="请输入手机号码" prefix-icon="el-icon-edit" v-model="sysUser.phone"></el-input>
                </el-form-item>
                <el-form-item label="住宅电话：" prop="telephone" :label-width="labelWidth">
                  <el-input placeholder="请输入住宅电话" prefix-icon="el-icon-edit" v-model="sysUser.telephone"></el-input>
                </el-form-item>
                <el-form-item label="联系地址：" prop="address" :label-width="labelWidth">
                  <el-input placeholder="请输入联系地址" prefix-icon="el-icon-edit" v-model="sysUser.address"></el-input>
                </el-form-item>
                <el-form-item label="备注：" prop="remark" :label-width="labelWidth">
                  <el-input placeholder="请输入备注" prefix-icon="el-icon-edit" v-model="sysUser.remark"></el-input>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer" style="text-align: right;margin-top: 20px;width: 100%;display: block">
                <el-button size="small" @click="cancelEidt()">取 消</el-button>
                <el-button size="small" type="primary" @click="sendMsg()">确 定</el-button>
              </span>
            </div>
          </div>
        </el-dialog>
      </div>
    </el-form>

  </div>
</template>
<script>
  export default{
    data(){
      return {
        labelWidth:'100px',
        tableLoading: false,
        fullloading: false,
        dialogTitle: '',//表单标题
        dialogVisible:false,//修改的表单的弹框
        addbool: false,
        keywords: '',
        sysUserList:[],
        sysUser:{
          id: null,
          name:"",//姓名,
          phone:"",//手机号码,
          telephone:"",//住宅电话,
          address:"",//联系地址,
          username:"",//用户名,
          password:"",//密码,
          userface:"",  //用户图像
          remark:"",//备注,
        },
        roleList: [],
        setRoleList: [],
        idstr:'',
        total: -1,  //总页数
        page: 1,  //当前页
        size:6,   //每页多少条
      }
    },
    mounted: function () {
      this.getList();
    },
    methods: {
      //获取列表
      getList(){
        let _this = this;
        this.tableLoading = true;
        this.$http.get("/system/sysUser/getAll").then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            if(resp.data.status == 1){//成功
              _this.sysUserList = resp.data.data; //列表数据
            }else{//失败
              this.$message.error("查询失败");
            }
          }
        }).catch((error=>{
          if (error.response.status == '403') {
            this.$router.push({path: '/forbidden403'}); // 对象
          }
        }))
      },

      //获取所有角色
      getAllRole(){
        let _this = this;
        this.$http.get("/system/role/getAll").then(resp=> {
          if (resp && resp.status == 200) {
            if(resp.data.status == 1){//成功
              _this.roleList = resp.data.data; //列表数据
            }else{//失败
              this.$message.error("查询失败");
            }
          }else if(resp && resp.status == 401){
            this.$message.error("没有权限");
          }
        })
      },

      //添加信息的预处理
      add(){
        this.sysUser = {};
        this.addbool = true;
        this.dialogVisible = true;
        this.dialogTitle = "新增";
      },
      //修改信息的预处理
      update(val,obj){
        this.dialogVisible = true;//弹框
        this.addbool = false;
        this.dialogTitle = obj.title;
        this.sysUser = window.clone(val);//克隆
      },

      searchClick(){
        alert("ff")
      },
      //选择框
      handleSelectionChange(val) {
        var idstr = "";
        for(var　i in val ){
          idstr+=val[i].id+","
        }
        this.idstr = idstr;
      },

      //取消调用弹框的方法
      cancelEidt(){
        this.dialogVisible = false;
      },
      sendMsg(){
        if(this.addbool){
          this.postMsg();
        }else{
          this.putMsg();
        }
      },

      //新增信息   post请求
      postMsg(){
        let md5 = require("crypto").createHash("md5").update(this.sysUser.password);
        this.sysUser.password = md5.digest('hex')
        let params = this.sysUser;
        this.$http({
          method:"post",
          url:"/system/sysUser/save",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'新增成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('新增失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //修改信息   put请求
      putMsg(){
        if (this.sysUser.password != null) {
          let md5 = require("crypto").createHash("md5").update(this.sysUser.password);
          this.sysUser.password = md5.digest('hex');
        }
        let params = this.sysUser;
        this.$http({
          method:"put",
          url:"/system/sysUser/update",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'修改成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //删除数据  delete请求
      deleteMsg(val){
        let _this = this;
        this.$confirm('确定删除此操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.$http({
            method:"delete",
            url:"/system/sysUser/delete/"+val
          }).then((response)=>{
            let data = response.data;
            if(data.status == 1){ //删除成功
              this.$message({
                message:'删除成功',
                type:'success'
              });
              this.getList();
            }else {
              data.msg!=null?this.$message.error(data.msg):this.$message.error('删除失败');
            }
          }).catch((error)=>{
            console.log(error);
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      deleteAll(){
        let ids = this.idstr;
        if(!ids){
          this.$message.error("提示，您还没有选择任何信息");
          return false;
        }else{
          this.deleteMsg(ids);
        }
      },

      //修改用户角色
      updateRole(val){
        let params = this.$qs.stringify({
          sysUserId:val,
          roleIds:this.setRoleList.toString()
        });
        this.$http({
          method:'put',
          url:'/system/sysUser/updateRoles',
          data:params
        }).then((response)=>{
          let data = response.data;
          if(data.status == 1){
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
        });
      },

      //加载原来的角色
      loadSetRoles(val){
        if(this.roleList.length == 0)
          this.getAllRole();
        this.setRoleList = [];
        val.forEach(role => {
          this.setRoleList.push(role.id);
        })
      },

    },

  }
</script>
<style lang="less">
  #userManage{
    .el-upload-list--picture-card .el-upload-list__item{
      width: 80px;
      height: 80px;
    }
    .el-upload--picture-card{
      width: 80px;height: 80px;
      line-height: 86px;
    }
    .el-upload-list--picture-card{
      float: left;
    }
    .el-form-item__label{
      color: #333333;
      font-weight: bold;
    }

    .el-input{
      width: 80%;
    }
    .el-dialog{
      margin-top: 5vh !important;
      min-width: 900px;
    }
  }

  #Bdialog{
    .H1{
      font: normal normal bold 28px/28px "Adobe 黑体 Std R";
      color: #333333;
      margin-bottom: 30px;
      margin-top: 5px;
    }
    .leader{
      text-align: left;
      margin: 20px 0 ;
      color: @warning;
      .l_bar{
        width: 7px;
        font-size: 18px;
        float: left;
        line-height: 18px;
        height: 21px;
        display: inline-block;
        background: @success;
        margin: 3px 3px 0 0;
      }
    }
    .dialog_B{
      max-height: 700px;
      padding-bottom: 30px;
      overflow-y: scroll;
    }
    .div_main{
      width: 700px;
      margin: auto;
      padding-bottom: 50px;
    }
    .Detail{
      .el-form-item__label{
        font-size: 16px;
        color: #333333;
      }
      .lb_v{
        font-size: 16px;
      }
    }
    .dialog_B::-webkit-scrollbar{
      display:none;
    }
    .el-form-item__content{
      text-align: left;
    }
    .el-dialog__body{
      padding: 6px 20px;
    }
  }

</style>



